.form-group {
  position: relative; }

.img {
  position: relative;
  cursor: pointer; }

.img img {
  width: 100%;
  border-radius: 8px;
  display: none; }

.img img[src] {
  width: 100%;
  border-radius: 8px;
  display: block; }

.img:hover:after {
  font-family: "Font Awesome 5 Free";
  font-style: normal;
  font-weight: 900;
  content: '\f2ed';
  height: 52px;
  background: #0275D8;
  display: block;
  position: absolute;
  bottom: 0;
  width: 100%;
  border-radius: 0 0 8px 8px;
  text-align: center;
  color: white;
  font-size: 20px;
  line-height: 52px; }

.upload-box {
  margin-top: 16px;
  border: 2px solid #000000;
  border-radius: 8px;
